import { Core } from '@walletconnect/core';
import { WalletKit } from '@reown/walletkit';

// Initialize the core instance with your WalletConnect project ID
const core = new Core({
  projectId: process.env.WALLET_CONNECT_PROJECT_ID, // Use your WalletConnect Project ID from .env
});

const metadata = {
  name: 'Blockchain Comment System',
  description: 'Decentralized comment system for websites with multi-wallet support',
  url: 'http://localhost', // If you don't have a domain, use localhost for development
  icons: ['images/BlockchainCommentIcon_128x128.png'], // Path to the icon image in the extension
};

async function initializeWalletKit() {
  try {
    // Initialize WalletKit with core and metadata
    const walletKit = await WalletKit.init({
      core, // Pass the core instance
      metadata,
    });

    // Ensure walletKit is correctly initialized before connecting wallets
    console.log('WalletKit initialized successfully:', walletKit);

    return walletKit;
  } catch (error) {
    console.error('Error initializing WalletKit:', error);
  }
}

async function connectWallet() {
  const walletKit = await initializeWalletKit();

  if (walletKit) {
    try {
      const walletConnection = await walletKit.connect(); // This line initiates wallet connection
      console.log('Wallet connected:', walletConnection);
    } catch (error) {
      console.error('Error connecting wallet:', error);
    }
  }
}

window.onload = function() {
  document.getElementById('connectWallet').addEventListener('click', connectWallet);
};


// Fetch comments from smart contract
async function fetchComments() {
  try {
    const urlHash = ethers.utils.keccak256(ethers.utils.toUtf8Bytes(window.location.href));
    const comments = await commentContract.getComments(urlHash);
    const commentSection = document.getElementById('commentSection');
    commentSection.innerHTML = '';

    if (comments.length > 0) {
      comments.forEach((commentHash, index) => {
        const commentDiv = document.createElement('div');
        commentDiv.className = 'comment';
        commentDiv.innerHTML = `<p><strong>Comment ${index + 1}</strong>: ${commentHash}</p>`;
        commentSection.appendChild(commentDiv);
      });
    } else {
      commentSection.innerText = 'No comments for this page.';
    }
  } catch (error) {
    console.error('Error fetching comments:', error);
  }
}

// Submit comment to smart contract
async function submitComment() {
  const newComment = document.getElementById('newComment').value;
  if (newComment.trim() === '') return alert('Comment cannot be empty');

  const submitButton = document.getElementById('submitComment');
  submitButton.innerText = 'Submitting...';

  try {
    const urlHash = ethers.utils.keccak256(ethers.utils.toUtf8Bytes(window.location.href));
    const commentHash = ethers.utils.keccak256(ethers.utils.toUtf8Bytes(newComment));

    await commentContract.addComment(urlHash, commentHash);

    // Reset input
    document.getElementById('newComment').value = '';
    alert('Comment added successfully!');

    // Fetch updated comments
    await fetchComments();
  } catch (error) {
    console.error('Error submitting comment:', error);
    alert('Failed to submit the comment.');
  } finally {
    submitButton.innerText = 'Submit Comment';
  }
}

// Initialize WalletKit on button click
window.onload = function () {
  document.getElementById('connectWallet').addEventListener('click', initWalletKit);
  document.getElementById('submitComment').addEventListener('click', submitComment);
};









